<template>
  <div class="movie">
    <ul>
      <li class="clearfix" v-for="item in dataList" :key="item.id" @click="intMovie(item.id)">
        <img :src="'https://images.weserv.nl/?url='+item.cover.url"/>
        <div class="right-content">
          <h3>{{item.title}}</h3>
          <div class="actor">
            主演:
            <span>{{item.actors.join(' ')}}</span>
          </div>
          <div class="key">
            关键字:
            <span>{{item.info}}</span>
          </div>
          <span>上映时间:{{item.year+'.'+item.release_date}}</span>
        </div>
      </li>
    </ul>
    <div class="layer" v-show="!isLoading">
      <div class="loading">
        <img class="loading-img" src="https://hbimg.huabanimg.com/1cd0c5b05f630a1cfd7a056ead30db9f0776cc9e51fe3-MpXEQu">
      </div>
    </div>
    <div class="tip" v-show="!isTip">
      <p>无法加载更多...</p>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data() {
    return {
      dataList: [],
      start: 0,
      //规定响应前不能再次请求数据 true代表可以发送请求
      isLoading: true,
      count: 10,
      total: 10,
      isTip: true,
    };
  },
  created() {
    let obj = {
      title: "电影",
      className: "movie"
    };
    this.$emit("changeNav", obj);
    this.getData();
  },
  methods: {
    getData() {
      let birdurl = "https://bird.ioliu.cn/v2?url=";
      let axiourl = `https://m.douban.com/rexxar/api/v2/subject_collection/movie_showing/items?start=${this.start}&count=10`;
      //调用这个方法的时候就不允许再次调用 直到响应结束
      this.isLoading = false;
      axios
        .get(birdurl + axiourl)
        .then((res) => {
          this.dataList = this.dataList.concat(res.data.subject_collection_items);
          this.total = res.data.total;
          //只有数据响应回来 才可以再次发送请求
          this.isLoading = true;
        })
        .catch(() => {
          return false;
        });
    },
    intMovie(id){
      this.$router.push({name: "detail",params: {id}});
    }
  },
  mounted() {
    window.onscroll = () => {
      //可视区域高度
      let clientHeight = document.documentElement.clientHeight;
      //当前滚动高度
      let scrollTop = document.documentElement.scrollTop;
      //滚动区域高度
      let scrollHeight = document.documentElement.scrollHeight;
      if(clientHeight + scrollTop >= scrollHeight - 10 && this.isLoading && this.count <= this.total){
        this.start += 10;
        this.count += 10;
        this.getData();
      }
      if(this.dataList.length == this.total){
        this.isTip = false;
      }
    };
  }
};
</script>
<style scoped>
* {
  margin: 0;
}
.movie ul {
  padding: 0;
}
li {
  list-style: none;
  padding: 7px 0;
  border-bottom: 1px solid #999;
}
img {
  width: 30%;
  float: left;
  padding-left: 3%;
}
.movie .right-content {
  float: left;
  font-size: 0.3rem;
  width: 60%;
  padding-left: 4%;
}
.clearfix::after {
  content: "";
  display: block;
  clear: both;
}
h3,
.key,
.actor {
  padding-bottom: 2px;
}
.movie {
  position: relative;
}
.movie .layer{
  width: 100vw;
  height: 83vh;
  background-color: rgba(255, 255, 255, 0.5);
  position: fixed;
  top: 1rem;
}
.movie .layer .loading-img{
  padding: 0;
  width: 200px;
  height: 100px;
  border-radius: 10px;
  position: absolute;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%)
}
.movie .tip{
  width: 100%;
  color: #000;
}
.movie .tip p{
  font-size: .2rem;
  text-align: center;
}
</style>
